<template>
  <el-menu
    :default-active="activeIndex"
    mode="horizontal"
    background-color="#545c64"
    text-color="#fff"
    active-text-color="#ffd04b"
    @select="handleSelect"
    router
  >
    <el-menu-item index="/">首页</el-menu-item>
    <el-menu-item index="/product">商品管理</el-menu-item>
    <el-menu-item index="/params">规格参数</el-menu-item>
    <el-menu-item index="/ad">广告管理</el-menu-item>
    <el-menu-item class="user">
      <span class="user-name">{{this.user.username}}</span>
      <el-button @click="logoutHandle">退出</el-button>
    </el-menu-item>
  </el-menu>
</template>

<script>
import {mapState,mapMutations} from 'vuex'

export default {
  data() {
    return {
      activeIndex: "/",
    };
  },
  mounted(){
    // 保证每次刷新高亮不会乱
    if(sessionStorage.getItem('key')){
      this.activeIndex = sessionStorage.getItem('key');
    }
  },
  computed:{
    ...mapState('login',['user'])
  },
  methods:{
    ...mapMutations('login',['setUser']),
    logoutHandle(){
      this.setUser({});
      localStorage.removeItem('ego');
      this.$router.push('/login')
    },
    handleSelect(key) {
      sessionStorage.setItem('key',key)
      }
  }
};
</script>

<style scoped lang="less">
.user {
  float: right !important;
  margin-right: 20px !important;
  line-height: 60px !important;
  .user-name {
    color: #fff;
    margin-right: 10px;
    font-size: 15px;
    border: 1px solid #fff;
    border-radius: 50%;
    width: 40px;
    display: inline-block;
    line-height: 40px;
    overflow: hidden;
  }
}
</style>